























































































import Vue from 'vue'

import {
  displayItem,
} from './scene_tiles'

import {
  BodyPart,

  Patrol,
  Explorer,
  Waiter,
  Chaser,
  Escaper,
  Loiter,
  Dispatcher,
  Picker,
  SelfHealer,
  Attacker,
  Thrower,
} from '../src/engine'

export default Vue.extend({
  name: 'Stats',
  props: ['creature'],
  computed: {
  },
  methods: {
    aiName(ai) {
      if (ai instanceof Patrol) {
        return 'Patrol'
      }
      if (ai instanceof Escaper) {
        return 'Escaper'
      }
      if (ai instanceof Chaser) {
        return 'Chaser'
      }
      if (ai instanceof Explorer) {
        return 'Explorer'
      }
      if (ai instanceof Waiter) {
        return 'Waiter'
      }
      if (ai instanceof Loiter) {
        return `Loiter`
      }
      if (ai instanceof Dispatcher) {
        return `Dispatcher (${this.aiName(ai.aiToRun)})`
      }
      if (ai instanceof Attacker) {
        return `Attacker`
      }
      if (ai instanceof SelfHealer) {
        return `SelfHealer`
      }
      if (ai instanceof Picker) {
        return 'Picker'
      }
      if (ai instanceof Thrower) {
        return 'Thrower'
      }
    },
    displayItem(item) {
      if (item) {
        return `${displayItem(item).getChar()} ${item.name}`
      }
    },
    displayAttribute(attribute) {
      if (attribute.currentValue() != attribute.maximum()) {
        return `${attribute.currentValue()} [${attribute.maximum()}]`
      } else {
        return attribute.currentValue()
      }
    },
    displayAttributeWithModifiers(attribute) {
      if (attribute.currentValue() != attribute.maximum()) {
        const mods = attribute.modifiers.map(x => x > 0 ? `+ ${x}` : `- ${x}`)
        return `${attribute.currentValue()} = [${attribute.maximum()}] ${mods}`
      } else {
        return attribute.currentValue()
      }
    },
    groupedCares(cares) {
      let grouped = {}

      cares.forEach(item => {
        if (grouped[item.name]) {
          grouped[item.name] += 1
        } else {
          grouped[item.name] = 1
        }
      })

      return grouped
    },
    displayBodyPart(bodyPart) {
      switch (bodyPart) {
        case BodyPart.LeftHand:
          return ''
        case BodyPart.RightHand:
          return ''
        case BodyPart.Legs:
          return ''
        case BodyPart.Finger:
          return ''
        case BodyPart.Head:
          return ''
        case BodyPart.Eye:
          return ''
        case BodyPart.Neck:
          return ''
        case BodyPart.Back:
          return ''
        case BodyPart.Body:
          return ''
        case BodyPart.MissileWeapon:
          return 'MissileWeapon'
        case BodyPart.Missile:
          return 'Missile'
        default:
          throw `Unknow body part ${bodyPart}`
      }
    }
  }
})
